* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}
html,
body {
  height: 100%;
  min-height: 100%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: "Microsoft YaHei", 'Source Sans Pro', 'Helvetica Neue', Helvetica, Arial, sans-serif;
  font-weight: 400;
  overflow: hidden;
}
::-webkit-scrollbar-thumb {
  background-color: #CCCCCC;
  height: 40px;
  outline-offset: -1px;
  outline: 1px solid #CCCCCC;
  -webkit-border-radius: 2px;
  border: 1px solid #CCCCCC;
}
::-webkit-scrollbar-thumb:hover {
  background-color: #6b6b6b;
  height: 40px;
  -webkit-border-radius: 2px;
}
::-webkit-scrollbar {
  width: 4px;
  height: 4px;
}
::-webkit-scrollbar-track-piece {
  background: rgba(37, 65, 136, 0.1);
  -webkit-border-radius: 0;
}
.anchorBL {
  display: none !important;
}
.breadcrumb {
  width: 100%;
  height: 52px;
  background: white;
  padding-top: 15px !important;
  padding-left: 20px !important;
}
.common-content {
  padding: 20px;
}
.common-title {
  font-size: 16px;
  color: rgba(0, 0, 0, 0.85);
  line-height: 24px;
  font-weight: bolder;
}
.common-title span {
  font-size: 12px;
  color: rgba(0, 0, 0, 0.65);
  line-height: 20px;
}
.text-center {
  text-align: center;
}
.text-right {
  text-align: right;
}
.ant-pagination-total-text {
  float: right !important;
  margin-left: 15px;
  line-height: 32px;
}
.maplable {
  border-collapse: collapse;
  border-spacing: none;
  border: 1px solid #ccc;
  width: 180px;
  font-size: 12px;
}
.maplable tr td:first-child {
  width: 50px;
}
.maplable tr td:last-child {
  width: 130px;
}
.maplable td {
  border: 1px solid #ccc;
  padding: 2px 3px;
}
.common-tooltip .ant-tooltip-inner {
  background-color: #309cff !important;
}
.common-tooltip .ant-tooltip-arrow {
  border-top-color: #309cff !important;
}
table {
  table-layout: fixed;
}
table td {
  text-overflow: ellipsis !important;
  white-space: nowrap !important;
  overflow: hidden !important;
}
